import { Company } from "../types";

export const getCompanyId = async (id: string): Promise<Company> => {
  return await fetch(
    `${process.env.NEXT_PUBLIC_API_MAIN}getCompany/${id}`
  ).then((response) => {
    if (!response.ok) {
      throw new Error("Network response was not ok");
    }
    return response.json();
  });
};
