"use client";
import { useCompaniesListById } from "../../company/queries/useCompaniesListById";
import { useAppointments } from "../queries/useAppointments";
import { AppointmentClient, AppointmentStatus } from "../types";

export const useFullAppointment = (
  status: AppointmentStatus
): AppointmentClient[] => {
  const {
    data: appointments = [],
    isLoading,
    isError,
  } = useAppointments({ status });

  // Early return for loading and error states
  if (isLoading || isError) {
    return []; // Or handle loading/error states appropriately
  }

  const companyIds = appointments.map((it) => it.companyId);

  const companyResults = useCompaniesListById(companyIds);

  const result = appointments.map((appointment, index) => {
    const companyData = companyResults[index].data;
    return {
      ...appointment,
      companyImg: companyData?.pictureUrl,
      companyName: companyData?.name,
      location: companyData?.location,
    } as AppointmentClient;
  });

  console.log(result, "result");
  return result;
};
